body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.image{
  height: 100vh;
  position: relative;
}
.content{
  height: 100vh;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content span{
  font-size: 1.5rem;
  font-weight: 500;
  background-color: #333333ce;
  color: white;
  padding: 1rem;
  text-transform: uppercase;
}

.text-box{
  text-align: center;
}
.text-box h3{
  font-size: 1.8rem;
  text-transform: uppercase;
}

.text-box h4{
  text-align: justify;
  font-weight: 400;
  padding: 5px 50px;
}